export default function RandomNumber() {
    const min = 1;
    const max = 37489;
    const rand = Math.floor(Math.random() * (max - min)) + min;
    return (rand);
}

export function RandomNum(length) {
    const min = 0;
    const max = length;
    const rand = Math.floor(Math.random() * (max - min)) + min;
    return (rand);
}
